*, :before, :after {
  box-sizing: border-box;
}

#browser-css-not-supported {
  display: none;
}

@supports not ((display: flex) and (transform: translate(50%, 50%))) {
  #stage {
    display: none !important;
  }

  #browser-css-not-supported {
    display: block !important;
  }
}

:root {
  --primary-base: #4f381b;
  --primary-contrast: #fff;
  --primary-highlight: #393;
  --secondary-base: #262673;
  --secondary-contrast: #fff;
  --secondary-highlight: #339;
  --tertiary-base: #732626;
  --tertiary-contrast: #fff;
  --tertiary-highlight: #933;
  --background-base: #f2f2f2;
  --background-contrast: #000;
  --background-highlight: #d9d9d9;
  --error-base: #862d2d;
  --error-contrast: #fff;
  --error-highlight: #c66;
  --window-base: #000;
  --window-contrast: #fff;
  --window-highlight: #e6e6e6;
  --padding-thickness: .25rem;
  --font-base-size: 15px;
  --line-thickness: max(2px, .2rem);
  --outline-thickness: 2px;
  --outline-offset: calc(.5 * var(--outline-thickness));
  --panel-border: solid var(--line-thickness);
  --margin-thickness: calc(var(--outline-offset)  + var(--line-thickness));
  --vh: 1vh;
}

body {
  color: var(--window-contrast);
  font-family: UnifrakturCook, cursive;
  font-size: calc(var(--font-base-size)  + .00390625 * var(--design-width));
  background-color: #000;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

#game-canvas {
  position: absolute;
}

#game-content {
  overflow: hidden;
}

.glass {
  opacity: 0;
  background-color: #000;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  width: 100vw;
  height: 100vh;
  transition: opacity .5s;
  position: absolute;
}

.glass-content {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  display: flex;
}

.glass.door {
  background-image: url("dungeon-splash.395230bd.jpg");
}

.glass.wall {
  background-image: url("wall.ac788cbf.jpg");
}

.glass-content > div {
  background-color: var(--window-base);
  border: 2px solid var(--window-contrast);
  color: var(--window-contrast);
  flex-direction: column;
  justify-content: center;
  gap: 4px;
  width: 70%;
  padding: .5rem;
  display: flex;
}

h1, h2, h3 {
  font-family: UnifrakturCook, cursive;
}

h1 {
  font-size: 2rem;
}

h2 {
  font-size: 1.5rem;
}

h3 {
  font-size: 1.2rem;
}

label {
  background-image: url("button-texture.ae62a420.jpg");
  justify-content: space-between;
  line-height: 48px;
  display: flex;
}

button {
  background-color: var(--primary-base);
  color: var(--primary-contrast);
  text-align: center;
  background-image: url("button-texture.ae62a420.jpg");
  font-family: UnifrakturCook, cursive;
  font-size: 1rem;
}

button:hover {
  cursor: pointer;
}

label .icon-button-external {
  background: none;
  border: none;
  margin: 0;
  padding: 0;
  line-height: 0;
}

.glass-content label:nth-of-type(2n) {
  background-color: #ffffff1a;
}

.icon-button-internal {
  flex-direction: row;
  place-content: center space-between;
  align-items: center;
  display: flex;
}

.close-button {
  color: #fff;
  text-align: center;
  background-color: #000;
  padding: 1px;
  position: absolute;
  top: 0;
  right: 0;
}

.close-button:before {
  content: "X";
}

.popup {
  opacity: 0;
  text-align: center;
  background-color: #000;
  border: 2px solid #fff;
  width: 75vw;
  transition: opacity 1s;
  position: absolute;
  top: 50vh;
  left: 50vw;
  transform: translate(-50%, -50%);
}

